/**
 * http 
 */
async function filterError(res) {
  if (res.code == 501) {
    this.app.changePage('login');
    throw new Error("")
  }

  if (res.code == 503) {
    this.app.alert.show({
      title: ' ',
      msg: '',
      close: true,
      footer: false,
      sure: ()=> {
        this.app.changePage('login');
      }
    })
    setTimeout(()=> {
      this.app.alert.hide()
      this.app.changePage('login');
    }, 5000)
    throw new Error(JSON.stringify(res))
  }

  if(res.code == -1){
    this.app.alert.show({
      title: ' ',
      msg: '',
      close: true,
      footer: false,
      sure: ()=> {

      }
    })
  }

  // if (res.code != 0) {
  //   this.app.alert.show({
  //     title: '',
  //     msg: res.msg,
  //     close: true,
  //     footer: true
  //   })
  //   throw new Error(JSON.stringify(res))
  // }
  return res
}
class HttpRequest {
  /**
* http post
* @method  POST
* @param  {object}  app  
* @param  {string}  url  
* @param  {JSON}  params  json
* @param  {string}  contentType 
* @return {promise}
*/
  async POST(url, params, domin = this.app.domain1, contentType = "application/json") {
    // app.loading.show()
    url = 'v1' + url// 
    let res = await ES.ajax({
      url: domin + url,
      type: "POST",
      dataType: "json",
      contentType,
      questring: params
    })
    // if (this.app.apiresult(res)) {
    //   return res
    // }
    return filterError.call(this, res)
  }
  async GET(url, params, domin = this.app.domain1, contentType = "application/json") {
    // app.loading.show()
    url = 'v1' + url// 
    let res = await ES.ajax({
      url: domin + url,
      type: "GET",
      dataType: "json",
      contentType,
      questring: params
    })
    // if (this.app.apiresult(res)) {
    //   return res
    // }
    return filterError.call(this, res)
  }

  /**
   *
   * @param {string} url
   * @param {object} header header
   */
  downLoadFile(url, header) {
    // url = '/v1' + url // 
    this.app.loading.show()
    let xhr = new XMLHttpRequest()
    xhr.open("GET", url, true)
    xhr.responseType = "blob";
    if (header) {
      xhr.setRequestHeader(header.key, header.val)
    }
    xhr.onreadystatechange = async () => {
      if (xhr.readyState === 4 && xhr.status === 200) {
        try {
          console.log(xhr.response)
            var reader = new FileReader()
            reader.onload = e => {
              console.log(e.target,e.target.result.charAt(0))
              if(e.target.result.charAt(0)=='{'){
                if(JSON.parse(e.target.result).code !== 0){
                  this.app.alert.show({
                    title: ' ',
                    msg: JSON.parse(e.target.result).msg,
                    close: false
                  })
                }
                this.app.loading.hide()
              }else{
                let blob = new Blob([xhr.response], { type: "application/vnd.ms-excel;charset=UTF-8" })
                if (typeof window.chrome !== 'undefined') {
                  let link = document.createElement('a')
                  link.download = xhr.getResponseHeader('content-disposition').split('=')[1];
                  link.href = window.URL.createObjectURL(blob)
                  link.click()
                  this.app.loading.hide()
                } else {
                  alert('Chrome')
                }
              }
            }
            reader.readAsText(xhr.response)
        } catch (err) {

        }
      }
    }
    xhr.onerror = async () => {
      // loadingHide
      this.app.loading.hide()
      reject(new Error(xhr.status || 'Server is fail.'));
    }
    xhr.send();

  }
}

module.exports = HttpRequest



